@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#drum-machine{background-color:lightgrey;width:600px;height:300px;display:grid;grid-template-columns:repeat(2, minmax(200px, 1fr));justify-content:center;align-content:center;outline:5px solid brown;font-family:"Bebas Neue", cursive;font-size:1.3em}#bankPad{display:grid;grid-template-rows:repeat(3, minmax(80px, 1fr));grid-template-columns:repeat(3, minmax(90px, 1fr));grid-column-gap:7px;-webkit-column-gap:7px;column-gap:7px;grid-row-gap:7px;row-gap:7px;margin-left:auto}#bankPad .drum-pad{background-color:grey;display:flex;align-items:center;justify-content:center;box-shadow:3px 3px #2d2424;border-radius:5px;cursor:pointer}#bankPad .drum-pad:active{background-color:#bd1616;box-shadow:3px 2px #2d2424;-webkit-transform:translateY(3px);transform:translateY(3px)}#controls-container{display:flex;flex-direction:column;justify-content:center;align-items:center}#controls-container .control{width:100px;margin:auto}#controls-container .control p{margin-bottom:0}#controls-container #display{width:200px;background-color:gray;margin:15px auto;padding:15px}.outer-select,#controls-container .control .select{margin:auto;border:1px solid black;width:50px;height:25px;padding:1px;background-color:black}.inner-select,#controls-container .control .select .inner{width:23px;height:19px;background:blue;border:1px solid black;cursor:pointer}@media screen and (max-width: 650px){.inner-container{-webkit-transform:scale(0.7);transform:scale(0.7)}}[type="range"]{-webkit-appearance:none;margin:12.5px 0;width:16em}[type="range"]:focus{outline:none}[type="range"]::-webkit-slider-runnable-track{width:16em;height:5px;cursor:pointer;-webkit-transition:all 0.2s ease;transition:all 0.2s ease;box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#424242;border:1px solid #000;border-radius:0}[type="range"]::-webkit-slider-thumb{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer;-webkit-appearance:none;margin-top:-11px}[type="range"]:focus::-webkit-slider-runnable-track{background:#4f4f4f}[type="range"]::-moz-range-track{width:16em;height:5px;cursor:pointer;-moz-transition:all 0.2s ease;transition:all 0.2s ease;box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#424242;border:1px solid #000;border-radius:0}[type="range"]::-moz-range-thumb{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer}[type="range"]::-ms-track{width:16em;height:5px;cursor:pointer;-ms-transition:all 0.2s ease;transition:all 0.2s ease;background:transparent;border-color:transparent;border-width:8px 0;color:transparent}[type="range"]::-ms-fill-lower{box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#353535;border:1px solid #000;border-radius:0}[type="range"]::-ms-fill-upper{box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#424242;border:1px solid #000;border-radius:0}[type="range"]::-ms-thumb{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer}[type="range"]:focus::-ms-fill-lower{background:#424242}[type="range"]:focus::-ms-fill-upper{background:#4f4f4f}

* {
  margin: 0;
  box-sizing: border-box;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: auto;
  height: 100vh;
  background-color: grey;
}


